import { requestip } from '../api/api.js'
export default{
	data(){
		return{
			data:'',
				num:0,
				level:'',
				datas:[]
		}
	},
	methods:{
		// 初始化数据
		Faninit(e){
			var uid=uni.getStorageSync('userId');
			uni.request({
				url: requestip()+'/v2/customer/wx/'+e.name+'?user_id='+uid,
				method: 'GET',
				data: {},
				success: res => {
					console.log('fans数据:',res.data.data.fans_list);
					this.data=res.data.data.fans_list.fan_count;
					console.log('fans数据:',res.data.data.fans_list.fans_list);
					this.datas=res.data.data.fans_list.fans_list;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		recall(n){
			uni.setClipboardData({
				data:n,
				success(res) {
					console.log('复制内容成功');
				}
			})
		}
	},
	onLoad:function(e){
		console.log('fans页面值：',e);
		this.num=e.num;
		this.level=parseInt(e.level)+1 ;
		this.Faninit(e)
	},
	onShareAppMessage() {
		return{
			title:'邀请链接',
			path:'pages/index/index?oparentid='+this.UserId+'&channel=1',
			imageUrl:'../../static/banner.png'
		}
	}
}
